import React, { useState } from "react";
import "../css/Userinfo.css";

const Userinfo = () => {
    // 상태 변수들
    const [nickname, setNickname] = useState("");
    const [email, setEmail] = useState("");
    const [password, setPassword] = useState("");
    const [passwordConfirm, setPasswordConfirm] = useState("");
    const [emailAgree, setEmailAgree] = useState(false);
    const [emailDisagree, setEmailDisagree] = useState(false);

    const handleSubmit = (e) => {
        e.preventDefault();
        // 여기서 수정된 정보를 서버로 전송하는 로직을 작성
        console.log("회원정보 수정됨:", { nickname, email, password, emailAgree });
    };

    return (
        <div className="user_info_container">
            <form className="user_info_form" onSubmit={handleSubmit}>
                <h2>회원정보 수정</h2>

                {/* 닉네임 */}
                <div className="form_group">
                    <label htmlFor="nickname">닉네임</label>
                    <input
                        type="text"
                        id="nickname"
                        value={nickname}
                        onChange={(e) => setNickname(e.target.value)}
                        required
                    />
                </div>

                {/* 비밀번호 */}
                <div className="form_group">
                    <label htmlFor="password">비밀번호</label>
                    <input
                        type="password"
                        id="password"
                        value={password}
                        onChange={(e) => setPassword(e.target.value)}
                        required
                    />
                </div>

                <div className="form_group">
                    <label htmlFor="passwordConfirm">비밀번호 확인</label>
                    <input
                        type="password"
                        id="passwordConfirm"
                        value={passwordConfirm}
                        onChange={(e) => setPasswordConfirm(e.target.value)}
                        required
                    />
                </div>

                {/* 이메일 */}
                <div className="form_group">
                    <label htmlFor="email">이메일</label>
                    <input
                        type="email"
                        id="email"
                        value={email}
                        onChange={(e) => setEmail(e.target.value)}
                        required
                    />
                </div>

                {/* 이메일 수신 동의 */}
                <div className="form_group">
                    <label>
                        <input
                            type="radio"
                            name="email_agree"
                            checked={emailAgree}
                            onChange={() => {
                                setEmailAgree(true);
                                setEmailDisagree(false);
                            }}
                        />
                        이메일 수신 동의
                    </label>
                    <label>
                        <input
                            type="radio"
                            name="email_agree"
                            checked={emailDisagree}
                            onChange={() => {
                                setEmailDisagree(true);
                                setEmailAgree(false);
                            }}
                        />
                        이메일 수신 거부
                    </label>
                </div>

                {/* 회원 탈퇴 */}
                <div className="form_group">
                    <button type="button" className="btn_delete_account">
                        회원 탈퇴
                    </button>
                </div>

                {/* 제출 버튼 */}
                <button type="submit" className="btn_update_info">
                    회원정보 수정
                </button>
            </form>
        </div>
    );
};

export default Userinfo;
